import { Button, buttonVariants, cn, Icon, Link } from "@shared/ui";
import { UserAccountDropdown } from "../user-account-dropdown";

import { Session, navItemType } from "../../types";
import React from "react";
import { ThemeToggle } from "../theme-togge/theme-toggle";
import { useGlobalStore } from "../../stores/globalStore";

interface DashboardTopbarProps {
  session: Session;
  onSignout: () => void;
}

const DashboardTopbar: React.FC<DashboardTopbarProps> = ({
  session,
  onSignout,
}) => {
  const navItems: navItemType[] = [];
  const { toggleSidebarOpen, sidebarOpen } = useGlobalStore();

  return (
    <div className="z-40 w-full bg-background flex justify-between lg:space-around items-center py-3 border-b px-8 lg:px-16 bg-nav-surface relative">
      <Button
        onClick={() => toggleSidebarOpen()}
        className="absolute left-0 "
        variant={"ghost"}
        size={"sm"}
      >
        <Icon
          name={sidebarOpen ? "PanelLeftClose" : "PanelLeft"}
          className=""
        />
      </Button>
      <div className="w-full flex gap-x-8 items-center">
        <div className="relative z-20 flex items-center">
          <Link
            href="/"
            className={cn(
              buttonVariants({ variant: "ghost" }),
              "text-md tracking-tighter"
            )}
          >
            <img src="/logo.png" alt="logo" className="icon-l mr-2" />
          </Link>
        </div>
        <div className="flex items-center gap-x-4">
          {navItems.map((item) => (
            <Link
              href={item.href}
              key={item.id}
              className={cn(
                buttonVariants({ variant: "ghost" }),
                "text-sm font-medium"
              )}
            >
              {item.label}
            </Link>
          ))}
        </div>
      </div>
      <div className="flex gap-x-4 items-center">
        <ThemeToggle />
        {session ? (
          <UserAccountDropdown session={session} onSignOut={onSignout} />
        ) : (
          <Link href="/sign-in" className=" tracking-tighter">
            <Button className="mx-2 w-full" size="sm">
              Sign In
              <div className="sr-only">Sign In</div>
            </Button>
          </Link>
        )}
      </div>
    </div>
  );
};

export { DashboardTopbar };
